*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-size: 62.5%; }
  @media only screen and (max-width: 62.5em) {
    html {
      font-size: 55%; } }

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.8rem;
  color: #00252e; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  color: inherit;
  font-weight: 600; }

a,
a:visited {
  text-decoration: none; }

ul {
  list-style: none; }
  ul a {
    color: #ebfbff; }

.header {
  background-color: #ebfbff;
  padding: 3rem 5rem; }
  @media only screen and (max-width: 37.5em) {
    .header {
      padding: 3rem 1rem; } }
  .header__intro {
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 6rem; }
  .header__logo-container img {
    width: 100%;
    vertical-align: middle; }
  @media only screen and (max-width: 37.5em) {
    .header__logo-container {
      flex-basis: 40%; } }
  .header__button-container {
    text-align: right; }
  .header__hero {
    display: flex;
    align-items: center;
    justify-content: space-between; }
    @media only screen and (max-width: 37.5em) {
      .header__hero {
        flex-direction: column;
        text-align: center; } }
  .header__hero-text-container {
    flex-basis: 43%; }
    .header__hero-text-container > *:not(:last-child) {
      margin-bottom: 4rem; }
    @media only screen and (max-width: 37.5em) {
      .header__hero-text-container {
        margin-bottom: 6rem; } }
  .header__hero-img-container {
    flex-basis: 54%; }

.main {
  padding: 10rem 5rem; }
  @media only screen and (max-width: 37.5em) {
    .main {
      padding: 10rem 2rem; } }
  .main__info-boxes > *:not(:last-child) {
    margin-bottom: 3rem; }
  .main__info-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 4rem 4rem 4rem 10rem;
    box-shadow: 0rem 0rem 2rem rgba(0, 0, 0, 0.1);
    border-radius: 1.5rem; }
    @media only screen and (max-width: 37.5em) {
      .main__info-box {
        flex-direction: column;
        text-align: center;
        padding: 4rem; } }
  .main__text-container {
    flex-basis: 49%; }
    .main__text-container > *:not(:last-child) {
      margin-bottom: 2rem; }
    @media only screen and (max-width: 37.5em) {
      .main__text-container {
        order: 2; } }
  .main__img-container {
    flex-basis: 40%; }
    @media only screen and (max-width: 37.5em) {
      .main__img-container {
        order: 1;
        margin-bottom: 4rem; } }

.footer a,
.footer svg,
.footer p {
  opacity: 0.87; }

.footer__cta {
  background-image: linear-gradient(180deg, #fff 70%, #00252e 0); }

.footer__box {
  width: 50%;
  padding: 3rem 5rem 5rem 5rem;
  box-shadow: 0rem 0rem 2rem rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  text-align: center;
  position: relative;
  background-color: #fff;
  border-radius: 1.5rem; }
  @media only screen and (max-width: 50em) {
    .footer__box {
      width: 70%; } }
  @media only screen and (max-width: 37.5em) {
    .footer__box {
      width: 90%;
      padding: 3rem 2rem; } }
  .footer__box > * {
    margin-bottom: 4rem; }

.footer__logo-container {
  margin-bottom: 3rem; }

.footer__contacts {
  background-color: #00252e;
  padding: 6rem 5rem;
  color: #ebfbff; }
  @media only screen and (max-width: 37.5em) {
    .footer__contacts {
      padding: 6rem 2rem; } }

.footer__info {
  display: flex;
  justify-content: space-between; }
  @media only screen and (max-width: 50em) {
    .footer__info {
      flex-direction: column; }
      .footer__info > *:not(:last-child) {
        margin-bottom: 4rem; } }

.footer__contact {
  flex-basis: 35%; }
  @media only screen and (max-width: 50em) {
    .footer__contact > *:not(:last-child) {
      margin-bottom: 2rem; } }

.footer__nav {
  flex-basis: 32%; }
  @media only screen and (max-width: 50em) {
    .footer__nav {
      flex-basis: 27%; } }

.footer__contact {
  margin-right: auto; }
  .footer__contact > *:not(:last-child) {
    margin-bottom: 1.5rem; }

.footer__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 2rem; }
  @media only screen and (max-width: 50em) {
    .footer__list {
      grid-row-gap: 1rem;
      grid-template-columns: none; } }

.footer__list-item > a {
  transition: all 0.2s; }
  .footer__list-item > a:hover {
    text-decoration: underline; }

.footer__social {
  flex-basis: 25%;
  display: flex;
  flex-direction: column; }
  @media only screen and (max-width: 50em) {
    .footer__social {
      text-align: center; } }

.footer__social-icons-container {
  text-align: center;
  display: flex;
  align-self: flex-start;
  margin-bottom: auto; }
  .footer__social-icons-container > .svg-container {
    border-radius: 50%;
    border: 1px solid #ebfbff;
    position: relative;
    width: 3rem;
    height: 3rem;
    cursor: pointer;
    transition: all 0.2s; }
    .footer__social-icons-container > .svg-container:hover {
      border-color: #ff52bf; }
      .footer__social-icons-container > .svg-container:hover > a svg {
        fill: #ff52bf; }
    .footer__social-icons-container > .svg-container > a svg {
      fill: #ebfbff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 1.5rem;
      height: 1.5rem;
      transition: all 0.2s; }
  @media only screen and (max-width: 50em) {
    .footer__social-icons-container {
      align-self: center;
      margin-bottom: 3rem; } }

.footer__copyright {
  font-size: 1.4rem; }

.container {
  max-width: 140rem;
  margin: 0 auto; }

.btn {
  padding: 1rem 5rem;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  border-radius: 5rem;
  box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
  cursor: pointer;
  outline: none;
  transition: all 0.2s; }
  .btn:hover {
    opacity: 0.8; }

.btn-white {
  background-color: #fff;
  color: #00252e;
  font-size: 1.5rem; }
  @media only screen and (max-width: 37.5em) {
    .btn-white {
      font-size: 1.3rem;
      padding: 1rem 3.5rem; } }

.btn-pink {
  background-color: #ff52bf;
  color: #fff;
  font-size: 1.4rem;
  padding: 1.5rem 5rem; }
  @media only screen and (max-width: 37.5em) {
    .btn-pink {
      padding-left: 6.5rem;
      padding-right: 6.5rem; } }

.img {
  display: block;
  width: 100%; }

.heading-primary {
  width: 90%;
  line-height: 1.3; }
  @media only screen and (max-width: 62.5em) {
    .heading-primary {
      font-size: 3rem; } }
  @media only screen and (max-width: 37.5em) {
    .heading-primary {
      width: 100%; } }

@media only screen and (max-width: 62.5em) {
  .heading-secondary {
    font-size: 2.5rem; } }

.contact-container {
  display: flex; }
  .contact-container > .svg-container {
    margin-right: 4.2rem; }

.svg-container {
  margin-right: 2rem; }

.u-o-1 {
  order: 1; }

.u-o-2 {
  order: 2; }
